.movies{
  width: 100%;
  padding: 0;
  margin: 0 auto;
}

.movies__add-button {
  width: 100%;
  margin: 0 auto;
  padding: 0 0 80px 0;
  display: flex;
  justify-content: center;
}


.movies__button {
  width: 320px;
  height: 36px;
  padding: 0;
  margin: 0;
  border: solid #e8e8e8;
  border-radius: 5px;
  background-color: #fff;
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  cursor: pointer;
}

@media screen and (max-width: 400px) {
  .movies__button {
    width: 240px;
  }
   
}
