.footer {
  height: 172px;
  background-color: #fafafa;
  width: 100%;
}

.footer__text {
  font-size: 13px;
  line-height: 16px;
  font-weight: 400;
  text-align: center;
  color: #a0a0a0;
  padding: 79px 0 20px 0;
  margin: 0;
}

.footer__border {
  background-color: #e8e8e8;
  height: 1px;
  width: calc(100% - 2 * 70px);
  margin: 0 auto;
  padding: 0;
}

.footer__navigation {
  display: flex;
  justify-content: space-between;
  padding: 20px 0 0 0;
  margin: 0;
  align-items: center;
}

.footer__copyright {
  color: #000;
  font-size: 13px;
  line-height: 16px;
  font-weight: 400;
  margin: 0;
  padding: 0 0 0 70px;
}

.footer__links {
  margin: 0;
  padding: 0 70px 0 0;
}

.footer__link {
  text-decoration: none;
  font-size: 13px;
  line-height: 16px;
  font-weight: 400;
  color: #000;
  margin: 0;
  padding: 0 20px 0 0;
}

.footer__link:last-of-type {
  padding: 0;
}

.footer__link:hover {
  opacity: 0.7;
  transition: opacity 0.4s linear 0.1s;
}

@media screen and (max-width: 768px) {
  .footer {
    background-color: #fff;
  }
  .footer__border {
    width: calc(100% - 2 * 30px);
  }
  .footer__copyright {
    padding: 0 0 0 30px;
  }
  .footer__links {
    padding: 0 30px 0 0;
  }
}

@media screen and (max-width: 320px) {
  .footer {
    height: 253px;
  }
  .footer__border {
    width: calc(100% - 2 * 10px);
  }
  .footer__text {
    font-size: 12px;
    line-height: 15px;
  }
  .footer__navigation {
    flex-direction: column-reverse;
    padding: 30px 0 0 0;
  }
  .footer__links {
    display: flex;
    flex-direction: column;
    height: 43px;
    margin: 0;
    padding: 0;
    justify-content: space-between;
    align-items: center;
  }
  .footer__copyright {
    padding: 0;
    padding: 30px 0 0 0;
    color: #A0A0A0;
    font-size: 12px;
    line-height: 15px;
  }
  .footer__link {
    padding: 0;
    font-size: 12px;
    line-height: 15px;
  }
}
