.form {
  margin: 0 auto;
  padding: 0;
  width: 100%;
  height: 700px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.form__greetings {
  padding: 0 0 20px 0;
  margin: 0 auto;
  width: 396px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.form__logo {
  width: 38px;
  height: 38px;
  padding: 0 0 40px 0;
  margin: 0;
}

.form__logo:hover {
  opacity: 0.8;
  transition: opacity 0.4s linear 0.1s;
}

.form__title {
  margin: 0;
  padding: 0;
  font-weight: 500;
  font-size: 24px;
  line-height: 29px;
}

.form__main {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 413px;
}

.form__fields {
  margin: 0 auto;
  padding: 0;
  border: none;
  display: flex;
  flex-direction: column;
  text-align: left;
  align-items: flex-start;
  width: 396px;
}

.form__field {
  margin: 0 auto;
  padding: 0;
  width: 100%;
}

.form__text {
  font-weight: 400;
  font-size: 10px;
  line-height: 12px;
  color: #a0a0a0;
  padding: 20px 0 10px 0;
  margin: 0;
}

.form__input {
  width: calc(100% - 2 * 15px);
  height: 46px;
  padding: 0 15px;
  margin: 0;
  background-color: #f9f9f9;
  border-radius: 8px;
  border: none;
}

.form__input:focus {
  outline: 1px solid #2be080;
}

.form__input_error {
  color: #ee3465;
}

.input__error {
  font-weight: 400;
  font-size: 10px;
  line-height: 12px;
  color: #ee3465;
  padding: 10px 0 0 0;
  margin: 0;
}

.form__submit {
  margin: 0;
  padding: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form__submit-button {
  width: 396px;
  height: 45px;
  padding: 0;
  margin: 0;
  color: #fff;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  background-color: #ff6838;
  border-radius: 3px;
  border: none;
  cursor: pointer;
}

.form__submit-button:hover {
  opacity: 0.8;
  transition: opacity 0.4s linear 0.1s;
}

.form__submit-button_disabled {
  background-color: #f8f8f8;
  color: #000;
}

.form__subtitle {
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #a0a0a0;
  padding: 16px 0 0px 0;
  margin: 0;
}

.form__link {
  color: #ff6838;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  padding: 0 0 0 6px;
  margin: 0;
}

.form__link:hover {
  opacity: 0.7;
  transition: opacity 0.4s linear 0.1s;
}

.form__status-request {
  color: #ff6838;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  padding: 10px 0;
  margin: 0;
}


@media screen and (max-width: 768px) {
  .form {
    height: 1024px;
  }
}

@media screen and (max-width: 448px) {
  .form {
    height: 780px;
  }
  .form__greetings {
    padding: 26px 0 60px 0;
    align-items: center;
    width: 260px;
    margin: 0 auto;
  }
  .form__main {
    height: 497px;
  }
  .form__fields {
    width: 260px;
  }
  .form__submit-button {
    width: 260px;
  }
}
