.techs {
  width: 100%;
  height: 625px;
  padding: 0;
  margin: 0 auto;
  background-color: #f5f5f5;
}

.techs__border {
  height: 150px;
  border-bottom: 1px solid #000;
  width: calc(100% - 2 * 70px);
  margin: 0 auto;
  display: flex;
}

.techs__title {
  padding: 100px 0 23px 0;
  font-size: 22px;
  line-height: 27px;
  font-weight: 400;
  margin: 0;
  letter-spacing: -0.04em;
}

.techs__content {
  width: calc(100% - 2 * 70px);
  max-width: 460px;
  display: flex;
  margin: 0 auto;
  padding: 90px 0 100px 0;
  flex-direction: column;
}

.techs__subtitle {
  font-size: 50px;
  font-weight: 400;
  line-height: 58px;
  text-align: center;
  padding: 0;
  margin: 0 auto;
  letter-spacing: -0.04em;
}

.techs__text {
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
  margin: 0 auto;
  padding: 26px 0 0 0;
  letter-spacing: -0.04em;
  text-align: center;
}

.techs__list {
  max-width: 695px;
  display: flex;
  list-style-type: none;
  padding: 0;
  margin: 0 auto;
  justify-content: space-around;
  flex-wrap: wrap;
}

.techs__list-item {
  width: 90px;
  height: 60px;
  border-radius: 10px;
  background-color: #e8e8e8;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  margin: 0 0 10px 0;
}

.techs__list-text {
  font-size: 14px;
  line-height: 17px;
  font-weight: 400;
  letter-spacing: -0.04em;
  padding: 0;
  margin: 0;
}

@media screen and (max-width: 768px) {
  .techs {
    height: 567px;
  }
  .techs__border {
    height: 140px;
    width: calc(100% - 2 * 50px);
  }
  .techs__content {
    width: calc(100% - 2 * 50px);
    padding: 80px 0 83px 0;
  }
  .techs__title {
    padding: 90px 0 23px 0;
  }
  .techs__text {
    padding: 22px 0 0 0;
    font-size: 12px;
    line-height: 18px;
  }
  .techs__list-item {
    width: 84px;
    height: 57px;
  }
  .techs__list {
    max-width: calc(100% - 2 * 60px);
  }
}

@media screen and (max-width: 520px) {
  .techs {
    height: 651px;
    background-color: #fff;
  }
  .techs__title {
    padding: 70px 0 28px 0;
    font-size: 18px;
    line-height: 22px;
  }
  .techs__border {
    width: calc(100% - 2 * 18px);
    height: 120px;
  }
  .techs__content {
    width: calc(100% - 2 * 14px);
    padding: 60px 0 50px 0;
  }
  .techs__subtitle {
    font-size: 30px;
    line-height: 36px;
  }
  .techs__text {
    font-size: 11px;
    line-height: 16px;
  }
  .techs__list {
    justify-content: space-between;
      max-width: calc(100% - 2 * 71px);
  }
  .techs__list-text {
    font-size: 12px;
    line-height: 15px;
  }
}
